.about {
  display: flex;
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;

  @media (max-width: $vp-1440) {
    padding: 0 25px;
  }

  @media (max-width: 1023px) {
    padding: 0 50px 0 46px;
  }

  @media (max-width: 767px) {
    padding: 0 25px;
  }
}

.about__image {
  margin: 0 100px 0 0;
  max-height: 320px;

  @media (max-width: 1023px) {
    margin: 0 29px 0 0;
  }

  @media (max-width: 767px) {
    display: none;
  }
}

.about__content {
  padding: 5px 0 20px 0;

  @media (max-width: 1023px) {
    padding: 0;
  }

}

.about__title {
  font-family: 'RobotoCondensed', "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #041827;
  margin: 0 0 15px 0;

  @media (max-width: 1023px) {
    margin: 0 0 10px 0;
  }

  @media (max-width: 767px) {
    font-size: 28px;
    line-height: 140.62%;
    margin: 0 0 15px 0;
  }
}

.about__description {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;

  &:not(:last-child) {
    margin: 0 0 20px 0;
  }

  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 134.4%;

    &:not(:last-child) {
      margin: 0 0 18px 0;
    }
  }
}

.about__description .is-closed {
  @media (max-width: 767px) {
    display: none;
  }
}

.about__description .is-opened {
  @media (max-width: 767px) {
    display: block;
  }
}

.about__accordion-button {
  font-family: 'RobotoCondensed', "Arial", sans-serif;
  font-style: normal;
  border: 1px solid #00B2FF;
  border-radius: 3px;
  width: 180px;
  height: 45px;
  font-weight: 700;
  font-size: 14px;
  text-align: center;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00B2FF;
  margin: 18px 0 0 0;

  &:hover {
    background-color: #2a4e69;
    color: #ffffff;
  }

  @media (max-width: 1023px) {
    margin: 0;
  }

  @media (max-width: 767px) {
    width: 160px;
    height: 35px;
  }
}